export function getDevices(state) {
    return state.devices
}

export function getSensors(state) {
    return state.selectedDeviceSensors
}

export function getMessages(state) {
    return state.sensorMessages
}

export function getNumberReport(state) {
    return state.numberReport
}

export function getGpsReport(state) {
    return state.gpsReport
}
